a:not(.no-style) {
  &,
  &:focus,
  &:hover,
  &:active {
    text-decoration: none;
    color: $colour-pinterest-red;
    border-bottom: 1px solid transparent;
    transition: border-bottom 150ms linear;
  }

  &:hover {
    border-bottom: 1px solid $colour-pinterest-red;
  }
}
