/**
 * Animation to rotate the loading spinner on pin previews
 */
@keyframes rotate40 {
  0% {
    transform: rotate(0deg);
  }
  @for $i from 1 to 10 {
    #{$i * 12.5%} {
      transform: rotate(#{$i * 45deg});
    }
  }
}
