.action-toolbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .send-all-to-board {
    display: flex;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);

    .select-container {
      padding-left: 10px;
    }
  }

  .send-to-pinterest {
    @include button-red('large');
    float: right;
    padding-left: 20px;
  }
}
